<template>
  <v-container>
    <v-layout row justify-center>
      <v-card>
        <v-toolbar color="primary" dark flat dense cad>
          <v-toolbar-title class="subheading">–°–æ–∑–¥–∞—Ç—å –≤–∏–∫—Ç–æ—Ä–∏–Ω—É</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-divider></v-divider>
        <v-card-text class>
          <v-form v-model="isValid" ref="form">
            <v-layout row wrap>
              <v-flex xs12 md6>
                <v-menu
                  ref="startMenu"
                  v-model="startMenu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="startDate"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="startDateFormatted"
                      label="–î–∞—Ç–∞ –Ω–∞—á–∞–ª–∞"
                      :rules="startDateRules"
                      prepend-icon="event"
                      @blur="startDate = parseDate(startDateFormatted)"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="startDate"
                    no-title
                    scrollable
                    :allowed-dates="allowedStartDates"
                    first-day-of-week="1"
                  >
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="startMenu = false">–û—Ç–º–µ–Ω–∞</v-btn>
                    <v-btn flat color="primary" @click="$refs.startMenu.save(startDate)">–û–∫</v-btn>
                  </v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12 md6>
                <v-menu
                  ref="endMenu"
                  v-model="endMenu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="endDate"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="endDateFormatted"
                      label="–î–∞—Ç–∞ –æ–∫–æ–Ω—á–∞–Ω–∏—è"
                      prepend-icon="event"
                      readonly
                      :rules="endDateRules"
                      @blur="endDate = parseDate(endDateFormatted)"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="endDate"
                    no-title
                    scrollable
                    :allowed-dates="allowedEndDates"
                    first-day-of-week="1"
                  >
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="endMenu = false">–û—Ç–º–µ–Ω–∞</v-btn>
                    <v-btn flat color="primary" @click="$refs.endMenu.save(endDate)">–û–∫</v-btn>
                  </v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  v-model="topCount"
                  :rules="topCountRules"
                  label="–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–ø –∏–≥—Ä–æ–∫–æ–≤"
                  prepend-icon="filter_1"
                />
              </v-flex>
            </v-layout>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="goBack">
            <v-icon left>arrow_back</v-icon>–ù–∞–∑–∞–¥
          </v-btn>
          <v-btn color="primary" flat @click="save" :disabled="isLoading" :loading="isLoading">
            <template v-slot:loader>
              <span class="custom-loader">
                <v-icon light>cached</v-icon>
              </span>
            </template>
            <v-icon left>save</v-icon>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
    <v-snackbar v-model="snackbar" bottom color="error">
      {{ snackbarText }}
      <v-btn flat @click="snackbar = false">
        <v-icon left>close</v-icon>–ó–∞–∫—Ä—ã—Ç—å
      </v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import Axios from "axios";
import apiEndpoints from "../../apiEndpoints";
export default {
  name: "NewQuiz",
  props: ["name"],
  title () {
    return `–î–æ–±–∞–≤–∏—Ç—å –≤–∏–∫—Ç–æ—Ä–∏–Ω—É - ${this.name}`
  },
  data: () => ({
    isValid: true,
    startMenu: false,
    endMenu: false,
    startDate: "",
    endDate: "",
    startDateFormatted: "",
    endDateFormatted: "",
    topCount: 10,
    isLoading: false,
    snackbar: false,
    snackbarText: ""
  }),
  computed: {
    computedStartDateFormatted() {
      return this.formatDate(this.startDate);
    },
    computedEndDateFormatted() {
      return this.formatDate(this.endDate);
    },
    startDateRules() {
      const rules = [];
      const requiredRule = value => {
        return value !== "" || "–£–∫–∞–∂–∏—Ç–µ –¥–∞—Ç—É –Ω–∞—á–∞–ª–∞ –≤–∏–∫—Ç–æ—Ä–∏–Ω—ã"
      };
      const lessThenEndDateRule = value => {
        if (this.endDate !== "" && value !== "") {
          let dateValue = new Date(this.parseDate(value));
          let endDateValue = new Date(this.endDate);
          return dateValue < endDateValue || "–î–∞—Ç–∞ –Ω–∞—á–∞–ª–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ä–∞–Ω—å—à–µ, —á–µ–º –¥–∞—Ç–∞ –∫–æ–Ω—Ü–∞";
        }
        return true;
      };
      rules.push(requiredRule);
      rules.push(lessThenEndDateRule);
      return rules;
    },
    endDateRules() {
      const rules = [];
      const requiredRule = value => {
        return value !== "" || "–£–∫–∞–∂–∏—Ç–µ –¥–∞—Ç—É –æ–∫–æ–Ω—á–∞–Ω–∏—è –≤–∏–∫—Ç–æ—Ä–∏–Ω—ã"
      };
      const moreThanStartDateRule = value => {
        if (this.startDate !== "" && value !== "") {
          let dateValue = new Date(this.parseDate(value));
          let startDateValue = new Date(this.startDate);
          return dateValue > startDateValue || "–î–∞—Ç–∞ –æ–∫–æ–Ω—á–∞–Ω–∏—è –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –ø–æ–∑–∂–µ, —á–µ–º –¥–∞—Ç–∞ –Ω–∞—á–∞–ª–∞";
        }
        return true;
      };
      rules.push(requiredRule);
      rules.push(moreThanStartDateRule);
      return rules;
    },
    topCountRules() {
      const rules = [];
      const requiredRule = value => {
        return value !== "" || "–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–ø –∏–≥—Ä–æ–∫–æ–≤"
      };
      const isNumberRule = value => {
        return !isNaN(value) || "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–ø –∏–≥—Ä–æ–∫–æ–≤ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å —á–∏—Å–ª–æ–º"
      };
      const moreThanZero = value => {
        if (value !== '' && !isNaN(value)) {
          return +value >= 0 || "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–ø –∏–≥—Ä–æ–∫–æ–≤ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –±–æ–ª—å—à–µ –∏–ª–∏ —Ä–∞–≤–Ω—ã–º –Ω—É–ª—é";
        }
      };
      rules.push(requiredRule);
      rules.push(isNumberRule);
      rules.push(moreThanZero);
      return rules;
    }
  },
  watch: {
    startDate(val) {
      this.startDateFormatted = this.formatDate(this.startDate);
    },
    endDate(val) {
      this.endDateFormatted = this.formatDate(this.endDate);
    }
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "channelQuizzes",
        params: { name: this.name }
      });
    },
    save() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isLoading = true;
      let apiUrl = apiEndpoints.channelQuizzes.replace(
        "channelName",
        this.name
      );
      let data = {
        startDate: this.startDateFormatted,
        endDate: this.endDateFormatted,
        topCount: this.topCount
      };
      Axios.post(apiUrl, data)
        .then(({ data }) => {
          this.goBack();
        })
        .catch(error => {
          if (error.response) {
            let error_str = "";
            error.response.data.errors.forEach(error => {
              error_str += error + "\n";
            });
            this.snackbarText = error_str;
            this.snackbar = true;
          }
        })
        .finally(() => {
          this.isLoading = false;
        })
    },
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}.${month}.${year}`;
    },
    parseDate(date) {
      if (!date) return null;
      const [day, month, year] = date.split(".");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    allowedStartDates: val => {
      let dateVal = new Date(val);
      let nowDate = new Date();
      return dateVal > nowDate;
    },
    allowedEndDates(val) {
      let dateVal = new Date(val);
      let nowDate = new Date();
      return dateVal > nowDate;
    }
  }
};
</script>

<style lang="scss">
