<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <v-container>
        <v-layout wrap>
            <v-flex xs12>
                <v-form ref="form">
                    <v-card>
                        <v-card-title primary-title>
                            <h3 class="headline mb-0">–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—ã–π –∫–∞–Ω–∞–ª</h3>
                            <span class="grey--text">–ü–µ—Ä–µ–¥ –∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ–º –ø–æ–ª—è, —Å–¥–µ–ª–∞–π—Ç–µ –∫–∞–Ω–∞–ª –ø—É–±–ª–∏—á–Ω—ã–º, –∑–∞–¥–∞–π—Ç–µ –µ–º—É —Å—Å—ã–ª–∫—É, –¥–æ–±–∞–≤—å—Ç–µ —Ç—É–¥–∞ –±–æ—Ç–∞ –∏ —Å–¥–µ–ª–∞–π—Ç–µ –µ–≥–æ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–æ–º.</span>
                        </v-card-title>
                        <v-card-text>
                            <v-text-field
                                :error-messages="newChannel.errorMessages"
                                prepend-icon="link"
                                v-model="newChannel.url"
                                :rules="rules"
                                label="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞–Ω–∞–ª –∏–ª–∏ —é–∑–µ—Ä–Ω—ç–π–º"></v-text-field>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn flat @click="save" —Åolor="primary"
                                    :disabled="loading"
                                    :loading="loading">
                                <template v-slot:loader>
                                    <span class="custom-loader">
                                        <v-icon light>cached</v-icon>
                                    </span>
                                </template>
                                <v-icon left>save</v-icon>
                                –î–æ–±–∞–≤–∏—Ç—å
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-form>
            </v-flex>
        </v-layout>
        <v-snackbar v-model="snackbar"
                    bottom
                    :color="snackbarColor">
            {{ snackbarText }}
            <v-btn
                   flat
                   @click="snackbar = false"><v-icon left>close</v-icon>–ó–∞–∫—Ä—ã—Ç—å</v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import axios from 'axios/index'
    import apis from '../../apiEndpoints'
    export default {
        name: "AddChannel",
        title: '–î–æ–±–∞–≤–∏—Ç—å –∫–∞–Ω–∞–ª',
        data: () => ({
            newChannel: {
                url: '',
                hasError: false,
                errorMessages: []
            },
            valid: true,
            snackbar: false,
            snackbarText: '',
            snackbarColor: 'info',
            loading: false
        }),
        computed: {
            rules() {
                const rules = [];
                const url_rule = value => {
                    let url_re = /https?:\/\/(t(elegram)?\.me|telegram\.org)\/([a-z0-9\_]){5,32}/gi;
                    let tg_username_re = /@[a-z0-9\_]/gi;
                    return url_re.test(value) || tg_username_re.test(value) || '–í–≤–µ–¥–∏—Ç–µ Telegram-—Å—Å—ã–ª–∫—É –Ω–∞ –≤–∞—à –∫–∞–Ω–∞–ª'
                };
                rules.push(url_rule);
                return rules;
            }
        },
        methods: {
            save () {
                if (!this.$refs.form.validate()) {
                    return;
                }
                this.loading = true;
                axios.post(apis.channels, {channelName: this.newChannel.url}).then(({data}) => {
                    if (data.errors) {
                        let error_str = '';
                        error.errors.forEach(error => {
                           error_str += error + '\n';
                        });
                        this.snackbarText = error_str;
                        this.snackbarColor = 'error';
                        this.snackbar = true;
                    }
                    this.$root.$emit('newChannelAdded', data);
                    this.newChannel.url = '';
                    this.snackbarText = `–î–æ–±–∞–≤–ª–µ–Ω –∫–∞–Ω–∞–ª ${data.channelTitle}`;
                    this.snackbarColor = 'success';
                    this.snackbar = true;
                }).catch((error) => {
                    if (error.response) {
                        let error_str = '';
                        error.response.data.errors.forEach(error => {
                           error_str += error + '\n';
                        });
                        this.snackbarText = error_str;
                        this.snackbarColor = 'error';
                        this.snackbar = true;
                    }
                }).finally(() => {
                    this.loading = false;
                });
            }
        }
    }
</script>

