<template>
	<v-card>
		<v-card-title class="headline">
			–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –¥–µ–π—Å—Ç–≤–∏–µ
		</v-card-title>
		<v-card-text>
			<p>–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ –±–µ–∑–≤–æ–∑–≤—Ä–∞—Ç–Ω–æ —É–¥–∞–ª–∏—Ç—å –≤–∏–∫—Ç–æ—Ä–∏–Ω—É <strong>{{ startDate }}</strong> - <strong>{{ endDate }}</strong>? –¢–∞–∫ –∂–µ —Å—Ç–∞–Ω—É—Ç –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã —Ä–µ–π—Ç–∏–Ω–≥–∏ –ø–æ —ç—Ç–æ–π –≤–∏–∫—Ç–æ—Ä–∏–Ω–µ.</p>
		</v-card-text>
		<v-card-actions>
			<v-spacer></v-spacer>
			<v-btn flat @click="$emit('cancel')" color="primary">–û—Ç–º–µ–Ω–∞</v-btn>
			<v-btn flat color="primary" @click="removeQuiz" :loading="loading" :disabled="loading">
				<template v-slot:loader>
          <span class="custom-loader">
            <v-icon light>cached</v-icon>
          </span>
        </template>
				–£–¥–∞–ª–∏—Ç—å
			</v-btn>
		</v-card-actions>
	</v-card>
</template>

<script>
	import Axios from 'axios';
	import apiEndpoints from '../apiEndpoints';
	export default {
		name: 'DeleteQuiz',
		data: () => ({
			quizId: 0,
			startDate: '',
			endDate: '',
			loading: false
		}),
		methods: {
			prepare(quiz) {
				this.quizId = quiz.id;
				this.startDate = quiz.startDate;
				this.endDate = quiz.endDate;
			},
			removeQuiz() {
				let apiUrl = apiEndpoints.quizzes.replace('quizId', this.quizId);
				this.loading = true;
				Axios.delete(apiUrl).then(() => {
					this.$emit('deleted', this.quizId)
				}).catch(error => {
					if (error.response) {
         		let errorMsgs = error.response.data;
         		let errorString = "";
         		errorMsgs.forEach(error => {
          		errorString += error + "\n";
         		});
         		this.$emit('error', errorString);
       		} else {
         		this.$emit('error', "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫...");
       		}
				}).finally(() => {
					this.loading = false;
				})
			}
		}
	}
</script>