<template>
    <v-container>
        <v-layout wrap>
            <v-flex xs12>
                <v-card>
                    <v-toolbar color="primary" dark flat dense cad>
                      <v-toolbar-title class="subheading">–ù–∞—Å—Ç—Ä–æ–π–∫–∏</v-toolbar-title>
                      <v-spacer></v-spacer>
                    </v-toolbar>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-form ref="form">
                            <v-text-field v-model="rightAnswerPoints"
                                          label="–û—á–∫–∏ –∑–∞ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –æ—Ç–≤–µ—Ç"
                                           :rules="rightAnswerPointsRules"></v-text-field>
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn flat color="primary" ripple @click="setSettings" :loading="loading" :disabled="loading">
                            <v-icon left>save</v-icon>
                            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
                            <template v-slot:loader>
                              <span class="custom-loader">
                                <v-icon light>cached</v-icon>
                              </span>
                            </template>
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-flex>
        </v-layout>
        <v-snackbar color="success" v-model="snackbar">
            –ù–∞—Å—Ç—Ä–æ–π–∫–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω—ã
        </v-snackbar>
    </v-container>
</template>

<script>
    import apiEndpoints from "../apiEndpoints";
    import axios from 'axios'
    export default {
        title: "–ù–∞—Å—Ç—Ä–æ–π–∫–∏",

        name: "Settings",
        data: () => ({
            rightAnswerPoints: 0,
            snackbar: false,
            loading: false
        }),
        methods: {
            getSettings() {
                let apiUrl = apiEndpoints.settings;
                axios.get(apiUrl).then(({data}) => {
                    this.rightAnswerPoints = data.rightAnswerPoints;
                });
            },
            setSettings() {
                if (!this.$refs.form.validate()) {
                    return;
                }
                this.loading = true;
                let data = {
                    rightAnswerPoints: this.rightAnswerPoints
                };
                let apiUrl = apiEndpoints.settings;
                axios.post(apiUrl, data).then(() => {
                    this.snackbar = true;
                }).finally(() => {
                    this.loading = false;
                })
            }
        },
        computed: {
            rightAnswerPointsRules() {
                const rules = [];
                const requiredRule = value => {
                    return value !== '' || "–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–∞–ª–ª–æ–≤ –∑–∞ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –æ—Ç–≤–µ—Ç";
                };
                const isNumberRule = value => {
                    return !isNaN(value) || "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–∞–ª–ª–æ–≤ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å —á–∏—Å–ª–æ–º"
                };
                const moreThanZero = value => {
                    if (value !== '' && !isNaN(value)) {
                        return +value > 0 || "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–∞–ª–ª–æ–≤ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –±–æ–ª—å—à–µ –Ω—É–ª—è";
                    }
                };
                rules.push(requiredRule);
                rules.push(isNumberRule);
                rules.push(moreThanZero);
                return rules;
            }
        },
        created() {
            this.getSettings();
        }
    }
</script>

